import { Injectable } from '@angular/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ModalInstance } from 'ngx-smart-modal/lib/services/modal-instance';

@Injectable({
  providedIn: 'root',
})
export class ModalsUtilitiesService {
  constructor(private ngxSmartModalService: NgxSmartModalService) {}

  isAnyModalOpened() {
    const openedModals = this.ngxSmartModalService.getOpenedModals();
    if (openedModals.length === 0) return false;
    return openedModals.length > 1 || !this.onlyHiddenScanToModal(openedModals);
  }

  private onlyHiddenScanToModal(openedModals: ModalInstance[]) {
    return (
      openedModals.length === 1 &&
      openedModals[0]?.id === 'ScanToModal' &&
      !!document.querySelector('.scan-to-modal-hidden')
    );
  }
}
